"use client"

import { useState } from "react"
import { JazonSidebar } from "@/components/jazon-sidebar"
import { JazonHeader } from "@/components/jazon-header"
import {
  SidebarInset,
  SidebarProvider,
} from "@/components/ui/sidebar"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
} from "@/components/ui/sheet"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { mockLeads, mockConversations, mockQualificationData } from "@/lib/mock-data"
import { Search, Filter, ArrowUpDown, Mail, Phone, MessageSquare, Clock } from "lucide-react"

export default function LeadsPage() {
  const [selectedLead, setSelectedLead] = useState<string | null>(null)
  const [filterStage, setFilterStage] = useState<string>("all")
  const [searchQuery, setSearchQuery] = useState("")

  const lead = selectedLead ? mockLeads.find(l => l.id === selectedLead) : null
  const conversations = selectedLead ? mockConversations[selectedLead] || [] : []
  const qualification = selectedLead ? mockQualificationData[selectedLead] : null

  const filteredLeads = mockLeads.filter(lead => {
    const matchesSearch = lead.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         lead.company.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStage = filterStage === "all" || lead.stage === filterStage
    return matchesSearch && matchesStage
  })

  const getIcpScoreColor = (score: number) => {
    if (score >= 85) return "text-chart-2"
    if (score >= 70) return "text-chart-4"
    return "text-muted-foreground"
  }

  const getIcpScoreBg = (score: number) => {
    if (score >= 85) return "bg-chart-2/10"
    if (score >= 70) return "bg-chart-4/10"
    return "bg-muted"
  }

  const getStageColor = (stage: string) => {
    switch (stage) {
      case "Qualification": return "default"
      case "Meeting Scheduled": return "default"
      case "Engaged": return "secondary"
      case "Disqualified": return "outline"
      default: return "outline"
    }
  }

  const getChannelIcon = (channel: string) => {
    if (channel.includes("Email")) return <Mail className="w-3 h-3" />
    if (channel.includes("Voice")) return <Phone className="w-3 h-3" />
    if (channel.includes("LinkedIn")) return <MessageSquare className="w-3 h-3" />
    return <Mail className="w-3 h-3" />
  }

  return (
    <SidebarProvider
      style={
        {
          "--sidebar-width": "calc(var(--spacing) * 72)",
          "--header-height": "calc(var(--spacing) * 12)",
        } as React.CSSProperties
      }
    >
      <JazonSidebar variant="inset" />
      <SidebarInset>
        <JazonHeader />
        <div className="flex flex-1 flex-col">
          <div className="flex flex-1 flex-col gap-4 p-4 md:gap-6 md:p-6">
            {/* Page Header */}
            <div className="flex flex-col gap-2">
              <h1 className="text-3xl font-semibold text-foreground">Leads</h1>
              <p className="text-sm text-muted-foreground">
                System of record for all leads with AI insights and recommendations
              </p>
            </div>

            {/* Filters */}
            <Card>
              <CardContent className="pt-6">
                <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
                  <div className="flex flex-1 gap-2">
                    <div className="relative flex-1 max-w-sm">
                      <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                      <Input
                        placeholder="Search leads..."
                        className="pl-8"
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                      />
                    </div>
                    <Select value={filterStage} onValueChange={setFilterStage}>
                      <SelectTrigger className="w-[180px]">
                        <Filter className="w-4 h-4 mr-2" />
                        <SelectValue placeholder="Filter by stage" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">All Stages</SelectItem>
                        <SelectItem value="Qualification">Qualification</SelectItem>
                        <SelectItem value="Engaged">Engaged</SelectItem>
                        <SelectItem value="Meeting Scheduled">Meeting Scheduled</SelectItem>
                        <SelectItem value="Research">Research</SelectItem>
                        <SelectItem value="Disqualified">Disqualified</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div className="flex items-center gap-2">
                    <Badge variant="secondary">{filteredLeads.length} leads</Badge>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Leads Table */}
            <Card>
              <CardHeader>
                <CardTitle>All Leads</CardTitle>
                <CardDescription>
                  Click any lead to view detailed timeline and AI decisions
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Lead</TableHead>
                      <TableHead>Company</TableHead>
                      <TableHead>
                        <div className="flex items-center gap-1">
                          ICP Score
                          <ArrowUpDown className="w-3 h-3" />
                        </div>
                      </TableHead>
                      <TableHead>Stage</TableHead>
                      <TableHead>Channel</TableHead>
                      <TableHead>Last Contact</TableHead>
                      <TableHead>AI Recommendation</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredLeads.map((lead) => (
                      <TableRow 
                        key={lead.id} 
                        className="cursor-pointer hover:bg-muted/50"
                        onClick={() => setSelectedLead(lead.id)}
                      >
                        <TableCell>
                          <div>
                            <p className="font-medium">{lead.name}</p>
                            <p className="text-xs text-muted-foreground">{lead.title}</p>
                          </div>
                        </TableCell>
                        <TableCell>
                          <div>
                            <p className="font-medium">{lead.company}</p>
                            <p className="text-xs text-muted-foreground">{lead.industry}</p>
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className={`flex items-center justify-center w-12 h-12 rounded-lg ${getIcpScoreBg(lead.icpScore)}`}>
                            <span className={`text-lg font-bold ${getIcpScoreColor(lead.icpScore)}`}>
                              {lead.icpScore}
                            </span>
                          </div>
                        </TableCell>
                        <TableCell>
                          <Badge variant={getStageColor(lead.stage)}>{lead.stage}</Badge>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-1.5">
                            {getChannelIcon(lead.channel)}
                            <span className="text-sm">{lead.channel}</span>
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
                            <Clock className="w-3 h-3" />
                            {lead.lastContact}
                          </div>
                        </TableCell>
                        <TableCell className="max-w-xs">
                          <p className="text-sm text-muted-foreground truncate">
                            {lead.aiRecommendation}
                          </p>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </div>
        </div>
      </SidebarInset>

      {/* Lead Detail Sheet */}
      <Sheet open={!!selectedLead} onOpenChange={(open) => !open && setSelectedLead(null)}>
        <SheetContent className="sm:max-w-2xl overflow-y-auto">
          {lead && (
            <>
              <SheetHeader>
                <SheetTitle>{lead.name}</SheetTitle>
                <SheetDescription>{lead.title} at {lead.company}</SheetDescription>
              </SheetHeader>

              <div className="mt-6 space-y-6">
                {/* ICP Score & Status */}
                <div className="grid grid-cols-2 gap-4">
                  <Card>
                    <CardHeader className="pb-3">
                      <CardTitle className="text-sm">ICP Score</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className={`text-3xl font-bold ${getIcpScoreColor(lead.icpScore)}`}>
                        {lead.icpScore}/100
                      </div>
                      <p className="text-xs text-muted-foreground mt-1">
                        {lead.icpScore >= 85 ? "Excellent fit" : lead.icpScore >= 70 ? "Good fit" : "Poor fit"}
                      </p>
                    </CardContent>
                  </Card>

                  <Card>
                    <CardHeader className="pb-3">
                      <CardTitle className="text-sm">Current Stage</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <Badge variant={getStageColor(lead.stage)} className="text-sm">
                        {lead.stage}
                      </Badge>
                      <p className="text-xs text-muted-foreground mt-2">
                        Last contact: {lead.lastContact}
                      </p>
                    </CardContent>
                  </Card>
                </div>

                {/* Triggers */}
                {lead.triggers.length > 0 && (
                  <Card>
                    <CardHeader>
                      <CardTitle className="text-sm">Detected Triggers</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className="flex flex-wrap gap-2">
                        {lead.triggers.map((trigger, idx) => (
                          <Badge key={idx} variant="secondary">{trigger}</Badge>
                        ))}
                      </div>
                    </CardContent>
                  </Card>
                )}

                {/* AI Recommendation */}
                <Card className="border-primary/20 bg-primary/5">
                  <CardHeader>
                    <CardTitle className="text-sm">AI Recommendation</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm">{lead.aiRecommendation}</p>
                  </CardContent>
                </Card>

                {/* Why Jazon Did This */}
                <Card>
                  <CardHeader>
                    <CardTitle className="text-sm">Why Jazon Recommended This</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <div>
                      <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1">
                        ICP Analysis
                      </p>
                      <p className="text-sm">
                        Score of {lead.icpScore} indicates {lead.icpScore >= 85 ? "strong alignment" : lead.icpScore >= 70 ? "moderate alignment" : "weak alignment"} with ideal customer profile based on company size, industry, and decision maker seniority.
                      </p>
                    </div>
                    <div>
                      <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1">
                        Engagement Signals
                      </p>
                      <p className="text-sm">
                        {lead.stage === "Qualification" && "High engagement across multiple channels indicates readiness for deeper qualification."}
                        {lead.stage === "Meeting Scheduled" && "Fully qualified with confirmed need, authority, and timeline."}
                        {lead.stage === "Engaged" && "Positive initial responses, continuing nurture sequence."}
                        {lead.stage === "Disqualified" && "No alignment with ICP criteria or budget constraints identified."}
                        {lead.stage === "Research" && "Validating ICP fit before initiating outreach."}
                      </p>
                    </div>
                  </CardContent>
                </Card>

                {/* Activity Timeline */}
                <Card>
                  <CardHeader>
                    <CardTitle className="text-sm">Activity Timeline</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      {conversations.length > 0 ? (
                        conversations.map((msg) => (
                          <div key={msg.id} className="flex gap-3 pb-4 border-b last:border-0">
                            <div className={`p-2 rounded-full h-fit ${
                              msg.channel === "email" ? "bg-primary/10 text-primary" :
                              msg.channel === "voice" ? "bg-chart-2/10 text-chart-2" :
                              "bg-chart-3/10 text-chart-3"
                            }`}>
                              {msg.channel === "email" && <Mail className="w-4 h-4" />}
                              {msg.channel === "voice" && <Phone className="w-4 h-4" />}
                              {msg.channel === "linkedin" && <MessageSquare className="w-4 h-4" />}
                            </div>
                            <div className="flex-1 space-y-1">
                              <div className="flex items-start justify-between">
                                <div>
                                  <Badge variant="outline" className="text-xs mb-1">
                                    {msg.channel} • {msg.direction}
                                  </Badge>
                                  {msg.subject && (
                                    <p className="font-medium text-sm">{msg.subject}</p>
                                  )}
                                </div>
                                <span className="text-xs text-muted-foreground">{msg.timestamp}</span>
                              </div>
                              {msg.summary ? (
                                <div className="bg-muted/50 rounded p-2 space-y-2">
                                  <p className="text-sm">{msg.summary}</p>
                                  {msg.duration && (
                                    <p className="text-xs text-muted-foreground">
                                      Duration: {msg.duration} • Outcome: {msg.outcome}
                                    </p>
                                  )}
                                  {msg.objections && msg.objections.length > 0 && (
                                    <div>
                                      <p className="text-xs font-medium mt-2 mb-1">Objections raised:</p>
                                      <ul className="text-xs text-muted-foreground space-y-1">
                                        {msg.objections.map((obj, idx) => (
                                          <li key={idx}>• {obj}</li>
                                        ))}
                                      </ul>
                                    </div>
                                  )}
                                </div>
                              ) : (
                                <p className="text-sm text-muted-foreground">{msg.content}</p>
                              )}
                            </div>
                          </div>
                        ))
                      ) : (
                        <p className="text-sm text-muted-foreground">No conversation history yet</p>
                      )}
                    </div>
                  </CardContent>
                </Card>

                {/* Qualification Details */}
                {qualification && (
                  <Card>
                    <CardHeader>
                      <CardTitle className="text-sm">Qualification Details</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-3">
                      {Object.entries({
                        "Need": qualification.need,
                        "Timeline": qualification.timeline,
                        "Authority": qualification.authority,
                        "Budget": qualification.budget
                      }).map(([key, data]) => (
                        <div key={key} className="pb-3 border-b last:border-0">
                          <div className="flex items-center justify-between mb-1">
                            <p className="text-sm font-medium">{key}</p>
                            <div className="flex items-center gap-2">
                              <Badge variant={data.known ? "default" : "outline"} className="text-xs">
                                {data.known ? "Confirmed" : "Unknown"}
                              </Badge>
                              <span className="text-xs text-muted-foreground">
                                {data.confidence}% confidence
                              </span>
                            </div>
                          </div>
                          <p className="text-sm text-muted-foreground">{data.value}</p>
                        </div>
                      ))}
                    </CardContent>
                  </Card>
                )}
              </div>
            </>
          )}
        </SheetContent>
      </Sheet>
    </SidebarProvider>
  )
}

